<style scoped>
  .time{
    font-size: 14px;
    font-weight: bold;
    text-align: left;
  }
  .title{
    padding-left: 5px;
    text-align: left;
  }
  .content{
    padding-left: 5px;
    text-align: left;
  }
</style>
<template>
  <div>
    <div  v-if="my.role === 1" style="width: 100%; height: 800px; background: #fff; overflow: auto; padding: 10px">
      <Timeline style="width: 100%">
        <TimelineItem v-for="item in showingNoticeList">
          <p class="time">{{ item.insertTime }}</p>
          <p class="title">{{ item.title }}</p>
          <p class="content">{{ item.content }}</p>
        </TimelineItem>
      </Timeline>
    </div>
    <div v-if="my.role === 0">
      <div>
        <!---->
        <div style="text-align: right; margin-bottom: 24px;">
          <Button
            type="success"
            size="large"
            style="margin-right: 10px;"
            @click="isAddModalVisible = true"
          >
            
          </Button>
          <Button
            type="error"
            size="large"
            style="margin-right: 10px;"
            :disabled="selectedNoticeList.length === 0"
            @click="handleMassDelete"
          >
            
          </Button>
        </div>

        <Table
          :columns="columns"
          :data="showingNoticeList"
          :loading="isTableLoading"
          @on-selection-change="selection => {this.selectedNoticeList = selection}"
        />

        <Page
          :total="totalNum"
          :page-size="pageSize"
          @on-change="showNoticeList"
          show-total
          show-elevator
          :styles="{ marginTop: '16px', textAlign: 'right' }"
        />
      </div>

      <!---->
      <Modal
        title=""
        v-model="isAddModalVisible"
        width="320"
        @on-ok="handleAdd"
        @on-cancel="resetAddForm"
      >
        <Form
          ref="addForm"
          :model="addForm"
          :rules="formRules"
          label-position="top"
          @submit.native.prevent
        >
          <FormItem
            label=""
            prop="title"
          >
            <Input
              v-model="addForm.title"
              placeholder=""
              :clearable="true"
            />
          </FormItem>

          <FormItem
            label=""
            prop="content"
          >
            <Input
              v-model="addForm.content"
              placeholder=""
              type="textarea"
            />
          </FormItem>
        </Form>
      </Modal>

      <!---->
      <Modal
        title=""
        v-model="isEditModalVisible"
        width="320"
        @on-ok="handleEdit"
        @on-cancel="resetEditForm"
      >
        <Form
          ref="editForm"
          :model="editForm"
          :rules="formRules"
          label-position="top"
          @submit.native.prevent
        >
          <FormItem
            label=""
            prop="title"
          >
            <Input
              v-model="editForm.title"
              placeholder=""
              :clearable="true"
            />
          </FormItem>

          <FormItem
            label=""
            prop="content"
          >
            <Input
              v-model="editForm.content"
              placeholder=""
              type="textarea"
            />
          </FormItem>
        </Form>
      </Modal>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Notice',

  data () {
    return {
      // 
      my: {},
      // 
      isTableLoading: false,

      // 
      pageSize: 8,

      // 
      pageNum: 1,

      totalNum: 0,

      // 
      isAddModalVisible: false,

      // 
      isEditModalVisible: false,

      // 
      noticeList: [],

      // 
      showingNoticeList: [],

      // 
      selectedNoticeList: [],

      // 
      columns: [
        {type: 'selection', width: 60, align: 'center'},
        {title: '', key: 'title', ellipsis: true},
        {title: '', key: 'content', ellipsis: true},
        {title: '', key: 'insertTime', maxWidth: 200},
        {
          title: '',
          align: 'center',
          width: 140,
          render: (h, params) => {
            //  JSX 
            const Button = 'Button'

            let index = params.index
            let id = params.row.id
            let name = params.row.name

            return (
              <div style="line-height: 48px;">
                <Button
                  type="primary"
                  size="small"
                  style="margin-right: 10px;"
                  onClick={() => { this.showEditModal(index) }}
                >
                  
                </Button>

                <Button
                  type="error"
                  size="small"
                  style="margin-right: 10px"
                  onClick={() => { this.handleDelete(id, name) }}
                >
                  
                </Button>
              </div>
            )
          }
        }
      ],

      // 
      addForm: {
        title: '',
        content: ''
      },

      // 
      editForm: {
        id: -1,
        title: '',
        content: ''
      },

      // 
      formRules: {

        // 
        title: [
          {type: 'string', required: true, message: ''},
          {max: 50, message: ''}
        ],

        // 
        content: [
          {type: 'string', required: true, message: ''},
          {max: 400, message: ''}
        ]
      }
    }
  },

  methods: {

    /**
     * 
     */
    fetchNoticeList: function () {
      this.isTableLoading = true

      this.utils.http.get('/api/notices', {
        pageNum: this.pageNum,
        pageSize: this.pageSize
      })
        .then(response => {
          switch (response.status) {
            case 0: {
              let noticeList = response.data.notices

              this.totalNum = response.data.count

              // 
              for (let i = 0; i < noticeList.length; i++) {
                let notice = noticeList[i]

                notice.insertTime = this.utils.moment(notice.insertTime).format('YYYY-MM-DD HH:mm:ss')
              }

              this.showingNoticeList = noticeList
              break
            }
            case 2: {
              this.utils.message.notLoggedIn()
              break
            }
            default: {
              this.utils.message.unknownError()
            }
          }

          this.isTableLoading = false
        })
        .catch(() => {
          this.utils.message.unknownError()
          this.isTableLoading = false
        })
    },

    /**
     * 
     *
     * @param pageNum 
     */
    showNoticeList: function (pageNum) {
      this.pageNum = pageNum

      let startIndex = (pageNum - 1) * this.pageSize
      let endIndex = startIndex + this.pageSize

      this.showingNoticeList = this.noticeList.slice(startIndex, endIndex)
    },

    /**
     * 
     *
     * @param id  ID
     */
    handleDelete: function (id) {
      this.$Modal.confirm({
        title: '',
        content: '',
        onOk: () => {
          this.requestDelete(id)
            .then(response => {
              switch (response.status) {
                case 0: {
                  this.$Message.success('')
                  this.fetchNoticeList()
                  break
                }
                case 2: {
                  this.utils.message.notLoggedIn()
                  break
                }
                default: {
                  this.utils.message.unknownError()
                  break
                }
              }
            }).catch(() => {
              this.utils.message.unknownError()
            })
        }
      })
    },

    /**
     * 
     *
     * @param {number} id  ID
     * @return {Promise}  resolve
     *  rejecterror cause = request
     */
    requestDelete: function (id) {
      return this.utils.http.delete('/api/notices/' + id)
    },

    /**
     * 
     */
    handleMassDelete: function () {
      // 
      this.$Modal.confirm({
        title: '',
        content: '' + this.selectedNoticeList.length + '',
        onOk: () => {
          //  ID List
          let idList = []
          for (let notice of this.selectedNoticeList) {
            idList.push(notice.id)
          }

          // 
          this.requestMassDelete(idList)
        }
      })
    },

    /**
     * 
     *
     * @param idList  ID 
     */
    requestMassDelete: function (idList) {
      this.utils.http.delete('/api/notices', {
        idList: JSON.stringify(idList)
      })
        .then(response => {
          switch (response.status) {
            case 0: {
              this.$Message.success('')
              this.fetchNoticeList()
              break
            }
            case 2: {
              this.utils.message.notLoggedIn()
              break
            }
            default: {
              this.utils.message.unknownError()
              break
            }
          }
        })
        .catch(() => {
          this.utils.message.unknownError()
        })
    },

    /**
     * 
     */
    handleAdd: function () {
      // 
      this.validateAdd()
      // 
        .then(() => { return this.requestAdd() })
        // 
        .then((response) => {
          switch (response.status) {
            // 
            case 0: {
              this.$Message.success('')
              this.resetAddForm()
              this.fetchNoticeList()
              break
            }
            // 
            case 2: {
              this.utils.message.notLoggedIn()
              break
            }
            default: {
              this.utils.message.unknownError()
              break
            }
          }
        })
        .catch((err) => {
          switch (err.cause) {
            case 'validate': {
              this.$nextTick(() => { this.isAddModalVisible = true })
              break
            }
            case 'request': {
              this.utils.message.unknownError()
              break
            }
            default: {
              break
            }
          }
        })
    },

    /**
     * 
     *
     * @returns {Promise}  resolve reject
     */
    validateAdd: function () {
      return new Promise((resolve, reject) => {
        this.$refs.addForm.validate()
          .then(passed => {
            if (passed) {
              resolve()
            }

            let err = new Error()
            err.cause = 'validate'
            reject(err)
          })
      })
    },

    /**
     * 
     *
     * @returns {Promise}  resolve
     *  rejecterror cause = 'request'
     */
    requestAdd: function () {
      let form = this.addForm

      return this.utils.http.post('/api/notices', {
        title: form.title,
        content: form.content
      })
    },

    /**
     * 
     */
    resetAddForm: function () {
      this.$refs.addForm.resetFields()
    },

    /**
     * 
     *
     * @param index  showingNoticeList 
     */
    showEditModal: function (index) {
      let notice = this.showingNoticeList[index]
      let form = this.editForm

      form.id = notice.id
      form.title = notice.title
      form.content = notice.content

      this.isEditModalVisible = true
    },

    /**
     * 
     */
    handleEdit: function () {
      // 
      this.validateEdit()
      // 
        .then(() => { return this.requestEdit() })
        // 
        .then(response => {
          switch (response.status) {
            case 0: {
              this.$Message.success('')
              this.fetchNoticeList()
              break
            }
            case 2: {
              this.utils.message.notLoggedIn()
              break
            }
            default: {
              this.utils.message.unknownError()
              break
            }
          }
        })
        .catch(err => {
          switch (err.cause) {
            case 'validate': {
              this.$nextTick(() => { this.isEditModalVisible = true })
              break
            }
            case 'request': {
              this.utils.message.unknownError()
              break
            }
            default: {
              break
            }
          }
        })
    },

    /**
     * 
     *
     * @return {Promise}  resolve reject
     */
    validateEdit: function () {
      return new Promise((resolve, reject) => {
        this.$refs.editForm.validate()
          .then(passed => {
            if (passed) {
              resolve()
            }

            let err = new Error()
            err.cause = 'validate'
            reject(err)
          })
      })
    },

    /**
     * 
     */
    requestEdit: function () {
      let form = this.editForm
      let url = '/api/notices/' + form.id

      return this.utils.http.put(url, {
        title: form.title,
        content: form.content
      })
    },

    /**
     * 
     */
    resetEditForm: function () {
      this.$refs.editForm.resetFields()
      this.editForm.id = -1
    },

    /**
     * 
     */
    fetchSelfInfo () {
      this.utils.http.get('/api/sessions')
        .then((response) => {
          let status = response.status
          switch (status) {
            case 0:
              this.my = response.data
              break
            case 2:
              this.utils.message.notLoggedIn()
              break
            default:
              this.utils.message.unknownError()
              break
          }
        })
    },
  },

  created () {
    this.fetchNoticeList()
    this.fetchSelfInfo()
  }
}
</script>



