<style lang="less">
  @import "message.less";
</style>
<template>
  <div style="width: 100%; height: 100%; background: #fff">
    <div style="width: 100%; height: 80%; overflow: auto">
      <div v-for="item in messageList" style="width: 100%; min-height: 150px;"
           :class="item.fromId === my.uid ? myMessageClass : messageClass">
        <Card style="width:350px; height: 90%; background: #f5f5f5">
          <p slot="title" style="text-align: left">
            <Icon type="person"></Icon>
            {{ item.fromName }}
          </p>
          <p slot="extra">
            {{ item.time }}
          </p>
          <p>
            {{ item.content }}
          </p>
          <p style="text-align: right">
            :{{ item.toName }}
          </p>
        </Card>
      </div>
    </div>
    <div class="ready_to_send">
      <textarea class="input_type" v-model="addModal.content" placeholder="..."></textarea>
      <div class="send_bar">
        <Select v-model="addModal.toId" style="width: 30%; text-align: center" clearable placeholder="">
          <Option v-for="item in userList" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
        <Button type="primary" style="margin-left: 10px" @click="sendMessage"></Button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Message',

  data () {
    return {
      // 
      my: {},
      // 
      isTableLoading: false,
      // 
      myMessageClass: 'my_message',
      // 
      messageClass: 'message',
      // 
      pageSize: 8,
      // 
      addModal: {
        content: '',
        toId: '',
        toName: ''
      },
      // 
      messageList: [],
      userList: [],

      // 
      showingMessageList: [],

      // 
      columns: [
        {title: '', key: 'name', ellipsis: true, maxWidth: 140},
        {title: '', key: 'phone', maxWidth: 200},
        {title: '', key: 'content', ellipsis: true}
      ]
    }
  },

  methods: {
    /**
     * 
     */
    fetchFromMessageList: function () {
      this.isTableLoading = true

      this.utils.http.get('/api/messages', {
        type: 'from',
        userId: this.my.uid
      })
        .then(response => {
          switch (response.status) {
            case 0: {
              this.messageList = response.data.messages
              this.fetchToMessageList()
              break
            }
            case 2: {
              this.utils.message.notLoggedIn()
              break
            }
            default: {
              this.utils.message.unknownError()
            }
          }

          this.isTableLoading = false
        })
        .catch(() => {
          this.utils.message.unknownError()
          this.isTableLoading = false
        })
    },

    /**
     * 
     */
    fetchToMessageList: function () {
      this.isTableLoading = true

      this.utils.http.get('/api/messages', {
        type: 'to',
        userId: this.my.uid
      })
        .then(response => {
          switch (response.status) {
            case 0: {
              this.messageList = this.messageList.concat(response.data.messages)
              let moment = this.utils.moment
              this.messageList.sort((o1, o2) => {
                return  moment(o1.time).unix() - moment(o2.time).unix()
              })
              for (let item of this.messageList){
                item.time = this.utils.moment(item.time).format('YYYY-MM-DD HH:mm:ss')
              }
              break
            }
            case 2: {
              this.utils.message.notLoggedIn()
              break
            }
            default: {
              this.utils.message.unknownError()
            }
          }

          this.isTableLoading = false
        })
        .catch(() => {
          this.utils.message.unknownError()
          this.isTableLoading = false
        })
    },

    /**
     * 
     */
    getAllUser () {
      this.utils.http.get('/api/users')
        .then((response) => {
          let status = response.status
          switch (status) {
            case 0:
              this.userList = this.dealUserList(response.data.users)
              break
            case 2:
              this.utils.message.notLoggedIn()
              break
            default:
              this.utils.message.unknownError()
              break
          }
        })
    },

    /**
     * ,  Select 
     * @param userList  List
     * @return {*}  List
     */
    dealUserList (userList) {
      for (let index = 0; index < userList.length; index++){
        if (userList[index].id === this.my.uid) {
          userList.splice(index, 1)
          break
        }
      }
      return this.utils.changeToSelectList(userList, 'id', 'name')
    },

    /**
     * 
     */
    fetchSelfInfo () {
      this.utils.http.get('/api/sessions')
        .then((response) => {
          let status = response.status
          switch (status) {
            case 0:
              this.my = response.data
              this.fetchFromMessageList()
              this.getAllUser()
              break
            case 2:
              this.utils.message.notLoggedIn()
              break
            default:
              this.utils.message.unknownError()
              break
          }
        })
    },

    /**
     * 
     */
    sendMessage: function () {
      if (this.addModal.content === '' || !this.addModal.content) {
        this.$Message.error('')
        return
      }

      if (this.addModal.toId === '' || !this.addModal.toId) {
        this.$Message.error('')
        return
      }

      this.$Modal.confirm({
        title: '',
        content: '',
        onOk: () => {
          this.utils.http.post('/api/messages', {
            fromId: this.my.uid,
            toId: this.addModal.toId,
            content: this.addModal.content,
          })
            .then((response) => {
              let status = response.status
              switch (status) {
                case 0:
                  this.$Message.success('')
                  this.addModal.content = ''
                  this.addModal.toId = ''
                  this.fetchFromMessageList()
                  break
                case 2:
                  this.utils.message.notLoggedIn()
                  break
                default:
                  this.utils.message.unknownError()
                  break
              }
            })
        }
      })
    }
  },

  created () {
    this.fetchSelfInfo()
  }
}
</script>



