import Axois from 'axios'
import Qs from 'qs'
import Moment from 'moment'

export default {
  /**
   * http  GET, PUT, POST, DELETE
   */
  http: {

    /**
     * GET 
     * @param url 
     * @param params 
     * @returns {*} 
     */
    get (url, params) {
      return ajax(url, 'GET', params)
    },

    /**
     * POST 
     * @param url 
     * @param params 
     * @returns {*} 
     */
    post (url, params) {
      return ajax(url, 'POST', params)
    },

    /**
     * PUT 
     * @param url 
     * @param params 
     * @returns {*} 
     */
    put (url, params) {
      return ajax(url, 'PUT', params)
    },

    /**
     * DELETE 
     * @param url 
     * @param params 
     * @returns {*} 
     */
    delete (url, params) {
      return ajax(url, 'DELETE', params)
    }
  },

  /**
   *  Select 
   * @param list 
   * @param label  label 
   * @param value  value 
   * @return {Array} 
   */
  changeToSelectList (list, value, label) {
    let selectList = []
    for (let item of list) {
      let newItem = {}
      newItem.value = item[value]
      newItem.label = item[label]
      selectList.push(newItem)
    }
    return selectList
  },

  moment: Moment,

  /**
   * 
   */
  message: {

    /**
     * 
     */
    notLoggedIn: function () {
      window.vm.$Message.error('')
      window.vm.$router.push({name: 'login'})
    },

    unknownError: function () {
      window.vm.$Message.error('')
    }
  }
}

function ajax (url, requestMethod, params = {}) {
  url = url.startsWith('/') ? '/' + url.substring(1) : url
  let contentType = 'application/x-www-form-urlencoded'
  let hasFile = false
  let method = requestMethod === 'GET' ? requestMethod : 'POST'
  if (requestMethod === 'PUT' || requestMethod === 'DELETE') {
    params._method = requestMethod
  }
  return new Promise((resolve, reject) => {
    // 
    Axois.request({
      url: url,
      method: method,
      headers: {
        'Content-Type': contentType
      },
      data: requestMethod === 'GET' ? undefined : params,
      params: requestMethod === 'GET' ? params : undefined,
      transformRequest: [
        (data) => hasFile ? data : Qs.stringify(data)
      ],
      timeout: 10000
    })
      .then((result) => {
        resolve(result.data)
      })
      .catch((error) => {
        error.cause = 'request'
        reject(error)
      })
  })
}



